<template>
    <div class="home">
        <div class="ball-container">
        <transition v-on:after-enter="afterEnter" v-on:enter="enter" v-on:before-enter="beforeEnter" name="drop" v-for="(litterBall,indexBall) in balls" :key="indexBall">
          <div v-show="litterBall.show" class="ball">
            <div class="inner" :class="indexBall">

            </div>
          </div>
        </transition>
      </div>
    </div>
     
</template>

<script>
  import axios from "axios";
  import $ from 'jquery';
  import Velocity from 'velocity-animate'
export default {
  name: 'home',

  data(){
      return{
           balls:[//
            {show:false,index:0},
            {show:false,index:1},
            {show:false,index:2},
            {show:false,index:3},
            {show:false,index:4},
            {show:false,index:5},
            {show:false,index:6},
            {show:false,index:7},
            {show:false,index:8},
            {show:false,index:9},
            {show:false,index:10},
            {show:false,index:11}
          ],
          dropBall:[] 
      }
  },
  methods:{//
    drop(el){//,el
        for(var i=0;i<this.balls.length;i++){
           if(!this.balls[i].show){//showfalsetrue
            let ball = this.balls[i];
            ball.show = true;  //this.ballstrue
            ball.el = el; //
            this.dropBall.push(ball);  //dropdropBall
            //console.log(this.dropBall,222)
            return  //
          }
        }
      },
    },
    methods:{
  //github
     beforeEnter(els){
              let count = this.dropBall.length -1 ;  //
              let ball = this.dropBall[count];  //show:true
                    let rect = ball.el.getBoundingClientRect();  //getBoundingClientRect()
                    var rect2 = els.getBoundingClientRect();
                    let x = rect.left -500 ; // - X
                    let y =-(window.innerHeight - rect.top -650) ;// -  - padding,marginy
                    //els.style.display ="";
                    els.style.opacity = 1;
                    els.style.webkitTransform = `translate3d(0,${y}px,0)`;
                    els.style.transform = `translate3d(0,${y}px,0)`; //

                    //
                    let inner = els.getElementsByClassName('inner')[0];
                    inner.style.webkitTransform = `translate3d(${x}px,0,0)`;
                    inner.style.transform = `translate3d(${x}px,0,0)`;
            },
            enter(els,done){
              var _this = this;
              let hh = els.offsetHeight; //
               _this.$nextTick(function(){
                  //
                  els.style.opacity = "0";
                   let inner = els.getElementsByClassName('inner')[0];
            Velocity(els,{translate3d:"translate3d(0,0,0)"},{duration:600});  // npm i velocity-animate --save   import Velocity from 'velocity-animate'
            Velocity(inner,{translate3d:"translate3d(0,0,0)"},{duration:600,complete:function(){
                        done();
                      }
                   });
             });


            },
            afterEnter(els){
                 let ball = this.dropBall.shift();
                  if(ball){
                    ball.show = false;
                    els.style.display = "none"; //
                  }
            },
    }
}
</script>
<style>
    .ball-container,
    .ball{
      position:fixed;
      left: 500px;
      bottom :650px;
      z-index :900;
      transform :translate3d(0,0,0);
      transition :all 0.6s cubic-bezier(0.49,-0.29,0.75,0.41);
        width :16px;
        height :16px;
        border-radius :50%;
        background :rgb(0,160,220);
        transition: all 0.6s linear;
        transform :translate3d(0,0,0);
    }
</style>



